		padding: 0;
}
.content .banner h1{
		position: absolute;
		width: 100%;
		top: 40%;
		z-index: 8;
		color: white;
		font-size: 66px;
        -webkit-animation: fadeIn 2s 0.5s ease both;
        animation: fadeIn 2s 0.5s ease both;
}
.introduce {
		position: relative;
		width: 100%;
		height: 100%;
		top: -10%;
}
.introduce img{
		width: 400px;
		height: 400px;
}
.pic{
		position: relative;
}
.pic img{
		//position: absolute;
