}
.ad{
		position: absolute;
		top: 10%;
		left: 55%;
        width: 336px;
}
.ad h3{
		text-align: left;
		font-size: 36px;
		color: #585858;
		font-weight: 500;
}
.word{
		font-size: 14px;
		color: #4b4b4b;
		line-height: 22px;
		text-align: left;
		margin: 0;
		padding: 0;
        padding-bottom: 10px;
}
.mar1{
		margin: 50px 0 50px;
}



