
.content .banner {
		position: relative;
		width: 100%;
		top: -13%;
		left: 0;
		margin: 0;
		padding: 0;
}
#about{
		text-align: left;
		position: absolute;
		width: 100%;
		top: 16%;
		left: 20%;
