
header {
        width: 100%;
        height: 100px;
        background: rgba(0, 0, 0, 0.6);
        position: fixed;
        top: 0;
        left: 0;
        z-index: 9;
        margin: 0;
        padding: 0;
}
#nav {
        position: absolute;
		width: 100%;
		height: 100%;
        top: 0;
        left: 0;
}
.logo {
        width: 54px;
        height: 54px;
        position: relative;
        left: 15%;
		top: 25%;
        cursor: pointer;
}
img {
        height: 100%;
        width: 100%;
        left: 10px;
        position: relative;
}
.nav-item ul {
        list-style: none;
        margin: 0;
        padding: 0;
		width:100%;
		height:100%;
        position: relative;
        left: 25%;
        bottom: 20px;
}
li {
        width: auto;
        height: 26px;
        line-height: 26px;
        position: relative;
        display: inline-block;
        margin-left: 44px;
        color: white;
        cursor: pointer;
        font-size: 16px;
        color: #fff;
        opacity: 0.8;
}
.selected {
        color: yellow;
}
@-webkit-keyframes navin{
0%{
            color: #a3daa5
}
100%{
            color: #0bfb13
}
}
@keyframes navin{
0%{
            color: #a3daa5
}
100%{
            color: #0bfb13
}
}
@-webkit-keyframes navout{
}
@keyframes navout{
}
.nav-in {
	  -webkit-animation:navin .5s 0s ease both;
			  animation:navin .5s 0s ease both;
}
.nav-out {
	  -webkit-animation:navout .5s 0s ease both;
			  animation:navout .5s 0s ease both;
}



